import React, { Component } from 'react';
import { connect } from 'react-redux';
import { updateBLIOT } from '../../../action/index'
import './iot.scss';
import { Table, Row, Col, Typography, Button } from 'antd';

class IOT extends Component {
    constructor(props) {
        super(props);
    }

    changeDevStatus = record => {
        const { iot } = this.props;
        const newData = iot.slice();
        newData.forEach(v => {
            if (v.name === record.name) {
                v.status = Number(!v.status);
            }
        });
        this.props.dispatch(updateBLIOT(newData));
    }

    render() {
        const { name } = this.props;
        const columns = [
            {
                dataIndex: 'name',
                title: '',
                width: 110,
                key: 'name',
            },
            {
                dataIndex: 'status',
                title: '',
                width: 130,
                key: 'status',
                render: (status, record) => {
                    if (record.name === '蓝牙绑定') {
                        return status ? <Typography.Text type='success'>已绑定</Typography.Text> : <Typography.Text type='danger'>未绑定</Typography.Text>
                    } else {
                        return status ?
                            <Typography.Text type='success'>已连接</Typography.Text> :
                            <Typography.Text type='danger'>未连接</Typography.Text>
                    }

                }
            },
            {
                dataIndex: 'status',
                title: '',
                width: 50,
                key: 'status',
                render: (status, record) => {
                    return status ?
                        <Button size='small' onClick={this.changeDevStatus.bind(this, record)}>断开</Button> :
                        <Button size='small' type='primary' onClick={this.changeDevStatus.bind(this, record)}>连接</Button>
                }
            },
            {
                dataIndex: 'last',
                title: '历史记录',
                width: 150,
                key: 'last',
            },
            {
                dataIndex: 'times',
                title: '使用次数',
                width: 110,
                key: 'times',
            }
        ];
        const { iot } = this.props;
        return (
            <div className='iot-wrap'>
                <Row
                    className='iot-top-bar'
                    wrap={false}
                    align='middle'>
                    <Col>
                        <Typography.Title className='iot-top-bar-name' level={5}>{name}</Typography.Title>
                    </Col>
                </Row>
                <Table
                    columns={columns}
                    dataSource={iot}
                    pagination={false}
                    rowKey={item => item.name} />
            </div>
        );
    }
}
export default connect(
    ({
        bingli: { id, name, iot }
    }) => {
        return {
            id,
            name,
            iot
        }
    },
    dispatch => ({ dispatch }),
)(IOT);