    onServHistoryDurationChange = v => {
        this.setState({
            sevDuration: null
        }, () => {
            setTimeout(() => {
                this.setState({
                    sevDuration: v
                });
            }, 500)
        });
    }

    filterTreeData = data => {
        return data.map(v => {
            if (v.children) {
                return {
                    ...v,
                    children: this.filterTreeData(v.children)
                };
            } else {
                v.title = <Collapse
                    expandIconPosition='right'
                    ghost
                    bordered={true}>
                    <Collapse.Panel header={v.title} key={v.key}>
                        {v.title}<br />
                        7:53 设置提醒吃药<br />
                        10:30 视频通话8分钟<br />
                        16:22 上传骨折视频<br />
                    </Collapse.Panel>
                </Collapse>
                return v;
            }
        })
    }

