import React, { Component } from 'react';
import E from 'wangeditor';
import { connect } from 'react-redux';
import './messages.scss';
import { Row, Col, Typography, Button, Drawer, List, Input, DatePicker } from 'antd';
import Message from './message';
import { addBLAPPMsg, addBLWCMsg, delBLWCMsg } from '../../../action/index'
class Messages extends Component {
    constructor(props) {
        super(props);
        this.state = {
            editor: null,
            notice: false,
            noticeTemp: '',
            noticeTempInput: '',
        };
        this.messagesRef = React.createRef();
    }

    componentDidMount() {
        const { type } = this.props;
        const editor = new E('#editor' + type);
        editor.config.menus = [
            'emoticon',
        ];
        editor.config.showFullScreen = false;
        editor.config.height = 150;
        editor.config.zIndex = 999;
        editor.create();
        this.setState({ editor });
    }

    componentWillUnmount() {
        const { editor } = this.state;
        editor && editor.destroy();
    }

    showNotice = () => {
        this.setState({
            notice: true
        });
    }

    closeNotice = () => {
        this.setState({
            notice: false,
            noticeTemp: null
        });
    }

    onNoticeTempChange = v => {
        this.setState({
            noticeTemp: v
        });
    }

    onNoticeTempInputChange = e => {
        this.setState({
            noticeTempInput: e.target.value || ''
        });
    }

    addNotice = () => {
        const { noticeTemp, noticeTempInput } = this.state;
        if (noticeTemp && noticeTempInput) {
            this.props.dispatch(addBLWCMsg(noticeTemp.format('YYYY/MM/DD HH:mm'), 'notice', noticeTempInput));
            this.setState({
                noticeTemp: null,
                noticeTempInput: null,
            });
        }
    }

    deleteNotice = (time) => {
        this.props.dispatch(delBLWCMsg(time));
    }

    send = () => {
        const { editor } = this.state;
        const { type } = this.props;
        const addFn = type === 'app' ? addBLAPPMsg : addBLWCMsg;
        let txt;
        if (txt = editor.txt.text()) {
            editor.txt.clear();
            this.props.dispatch(addFn(txt, 'self'));
            this.messagesRef.current.scrollTop = 9999999;
            if (type === 'app') {
                setTimeout(() => {
                    this.props.dispatch(addFn(txt + '的回复', 'other'));
                    this.messagesRef.current.scrollTop = 9999999;
                }, 1000);
            }
        }
    }

    render() {
        const { type, appMessages, wcMessages, name } = this.props;
        const { notice, noticeTemp, noticeTempInput } = this.state;
        const list = type === 'app' ? appMessages : wcMessages;
        return (
            <div className='messages-wrap'>
                <Row
                    className='message-top-bar'
                    wrap={false}
                    align='middle'>
                    <Col>
                        <Typography.Title className='message-top-bar-name' level={5}>{name}</Typography.Title>
                    </Col>
                </Row>
                <div className='message-list' ref={this.messagesRef}>
                    {type === 'wc' && <Button onClick={this.showNotice}>设定通知</Button>}
                    {
                        list.map(v => {
                            return <Message role={v.role} key={v.time}>{v.text}</Message>
                        })
                    }
                </div>
                <div className='editor-wrap'>
                    <div id={'editor' + type}></div>
                    <Button type='primary' className='editor-send' onClick={this.send}>发送</Button>
                </div>
                <Drawer
                    title='设置提醒'
                    onClose={this.closeNotice}
                    width={300}
                    visible={notice}>
                    <List
                        header={<Button size='small' onClick={this.addNotice}>新增</Button>}>
                        <List.Item>
                            <div>
                                <DatePicker
                                    showTime
                                    value={noticeTemp}
                                    placeholder='选择提醒时间'
                                    style={{ width: '100%' }}
                                    onChange={this.onNoticeTempChange} />
                                <Input.TextArea
                                    value={noticeTempInput}
                                    style={{ width: '100%' }}
                                    placeholder='设置提醒内容'
                                    onChange={this.onNoticeTempInputChange} />
                            </div>
                        </List.Item>
                        {
                            list.filter(v => v.role === 'notice').map(v => <List.Item
                                key={v.time}
                                actions={[<Button size='small' onClick={this.deleteNotice.bind(this, v.time)}>删除</Button>]}>
                                <List.Item.Meta
                                    title={v.text + '用药提醒'}>
                                </List.Item.Meta>
                            </List.Item>)
                        }

                    </List>
                </Drawer>
            </div>
        );
    }
}

Messages.defaultProps = {
    type: 'app'
}

export default connect(
    ({
        bingli: { appMessages, wcMessages, id, name }
    }) => {
        return {
            appMessages,
            wcMessages,
            id,
            name,
        }
    },
    dispatch => ({ dispatch }),
)(Messages);