import React, { Component } from 'react'
import { connect } from 'react-redux';
import { Input, Table, Typography } from 'antd';
import './bingren.scss';
import { updateBL } from '../../../action/index';
const { Search } = Input;

const columns = [
    {
        title: '序号',
        dataIndex: 'id',
        width: 50,
        align: 'center',
        key: 'id',
        render(id, r, i) {
            return i + 1;
        }
    },
    {
        title: '编号',
        dataIndex: 'id',
        align: 'center',
        key: 'id',
    },
    {
        title: '姓名',
        dataIndex: 'name',
        key: 'name',
    },
];

class BingRenList extends Component {
    constructor(props) {
        super(props);
        this.state = {
            keyWord: ''
        };
    }

    componentDidMount() {
        const { id, data } = this.props;
        id || this.onRowClick(data[0]);
    }

    onSearch = keyWord => {
        this.setState({
            keyWord: keyWord || ''
        });
    }

    filterDataFn = () => {
        const { data } = this.props;
        const { keyWord } = this.state;
        if (keyWord === '') {
            return data;
        }
        return data.filter(b => b.name.indexOf(keyWord) >= 0);
    }

    onRowClick = ({ id, name }) => {
        this.props.dispatch(updateBL({ id, name }));
    }

    render() {
        const filterData = this.filterDataFn();
        const { id, name } = this.props;
        return (
            <>
                <Typography.Title level={4} align='center' className='bingren-title'>患者列表</Typography.Title>
                <Search
                    placeholder='搜索病人姓名'
                    allowClear
                    enterButton='搜索'
                    size='middle'
                    onSearch={this.onSearch}
                />
                <Table
                    size='small'
                    rowKey='id'
                    dataSource={filterData}
                    columns={columns}
                    pagination={false}
                    rowClassName={item => {
                        if (item.id === id) {
                            return 'selected-bl';
                        }
                    }}
                    onRow={record => {
                        return {
                            onClick: this.onRowClick.bind(this, record), // 点击行
                        };
                    }}
                />
            </>
        );
    }
}

BingRenList.defaultProps = {
    data: [
        {
            id: '001',
            name: '张春华'
        },
        {
            id: '002',
            name: '李健'
        },
        {
            id: '004',
            name: '吴磊'
        },
        {
            id: '008',
            name: '陈晨'
        },
        {
            id: '011',
            name: '王大发'
        },
        {
            id: '089',
            name: '唐器'
        },
        {
            id: '079',
            name: '于国盛'
        }
    ]
}

export default connect(
    ({
        bingli: { id, name }
    }) => {
        return {
            id,
            name
        }
    },
    dispatch => ({ dispatch }),
)(BingRenList);