import React, { Component } from 'react';
import './liveroom.scss';

import { Modal, Button, Row, Col, Input } from 'antd';
import AntdDraggableModal from 'antd-draggable-modal';

class LiveRoom extends Component {
    constructor(props) {
        super(props);
        this.state = {
            mediaStreamTrack: null,
            muted: false,
            volumn: false,
            fullScreen: false,
        };
    }

    componentDidMount() {
        //this.initLiveRoom();
    }

    componentWillUnmount() {
        const { mediaStreamTrack } = this.state;
        this.getLiveRoomVideo().pause();
        if (mediaStreamTrack) {
            mediaStreamTrack.stop();
        }
    }

    getLiveRoomVideo = () => {
        return document.getElementById('liveRoomVideo');
    }

    changeFullScreen = () => {
        const { fullScreen } = this.state;
        this.setState({
            fullScreen: !fullScreen
        });
    }

    onCancel = () => {
        const { onCancel } = this.props;
        onCancel();
    }

    initLiveRoom = opt => {
        opt = {
            audio: true,
            video: {
                width: 100,
                height: 150
            }
        };
        navigator.mediaDevices.getUserMedia(opt)
            .then(mediaStream => {
                const video = this.getLiveRoomVideo();
                const mediaStreamTrack = typeof mediaStream.stop === 'function' ? mediaStream : mediaStream.getTracks()[1];
                video.srcObject = mediaStream;
                video.onloadedmetadata = e => {
                    video.play();
                };
                this.setState({
                    mediaStreamTrack,
                });
            })
            .catch(err => {
                console.error(err.name + ': ' + err.message);
            });
    }

    render() {
        const { fullScreen } = this.state;

        return (
            <AntdDraggableModal
                visible={true}
                width={400}
                title='视频通话<按住可拖拽>'
                onCancel={this.onCancel}
                bodyStyle={{ padding: 0 }}
                style={{
                    position: 'absolute',
                    top: 0,
                    right: 0
                }}
                mask={false}
                maskClosable={false}
                closable={false}
                footer={null}>
                <div className='live-room-wrap'>
                    <video
                        className='live-room-video'
                        id='liveRoomVideo'
                        onDoubleClick={this.changeFullScreen}
                    >
                    </video>
                    <div className='bingren-video'>
                        患者视频展示区域
                    </div>
                    <Input.TextArea placeholder='医生输入区域' className='textarea-doc'></Input.TextArea>
                    <Row className='footer' justify='space-between'>
                        <Col><Button type='primary'>关闭麦克风</Button></Col>
                        <Col>
                            <Button
                                danger
                                onClick={this.onCancel}
                                type='primary'>挂断
                            </Button>
                        </Col>
                        <Col><Button>静音</Button></Col>
                    </Row>
                </div>
            </AntdDraggableModal>
        );
    }
}

export default LiveRoom;