import {
    CHANGE_BL_DETAIL,
    CHANGE_BL_ID,
    CHANGE_BL_IOT,
    CHANGE_BL_MESSAGE_APP,
    CHANGE_BL_MESSAGE_WC,
    ADD_BL_MESSAGE_APP,
    ADD_BL_MESSAGE_WC,
    DEL_BL_MESSAGE_WC,
} from './action-type';

export const updateBL = ({ id, name }) => dispatch => {
    dispatch({
        type: CHANGE_BL_ID,
        data: { id, name }
    });
}

export const updateBLDetail = detail => dispatch => {
    dispatch({
        type: CHANGE_BL_DETAIL,
        data: detail
    });
}

export const updateBLAPPMsgs = (list = []) => dispatch => {
    dispatch({
        type: CHANGE_BL_MESSAGE_APP,
        data: list
    });
}

export const addBLAPPMsg = (msg, role) => dispatch => {
    return dispatch({
        type: ADD_BL_MESSAGE_APP,
        data: {
            text: msg,
            time: Date.now(),
            role
        }
    });
}

export const updateBLWCMsgs = (list = []) => dispatch => {
    dispatch({
        type: CHANGE_BL_MESSAGE_WC,
        data: list
    });
}

export const addBLWCMsg = (msg, role, content) => dispatch => {
    return dispatch({
        type: ADD_BL_MESSAGE_WC,
        data: {
            text: msg,
            content,
            time: Date.now(),
            role
        }
    });
}

export const delBLWCMsg = time => dispatch => {
    return dispatch({
        type: DEL_BL_MESSAGE_WC,
        data: {
            time
        }
    });
}

export const updateBLIOT = iot => dispatch => {
    dispatch({
        type: CHANGE_BL_IOT,
        data: iot
    });
}