import React, { Component } from 'react';
import './message.scss'
import { Row, Col, Avatar } from 'antd';
import AvatarImage from '../../../assets/avatar-1.jpeg';

const roleMap = {
    self: {
        align: 'end'
    },
    other: {
        align: 'start'
    },
    notice: {
        align: 'center'
    },
    'notice-del': {
        align: 'center'
    }
}
class Message extends Component {
    constructor(props) {
        super(props);
    }

    getReactNode = (role, props) => {
        switch (role) {
            case 'self':
                return <>
                    <Col className='message-item-text'>{props.children}</Col>
                    <Col>
                        <Avatar
                            size={40}
                            src={AvatarImage} />
                    </Col>
                </>
                break;
            case 'other':
                return <>
                    <Col>
                        <Avatar
                            size={40}
                            src={AvatarImage} />
                    </Col>
                    <Col className='message-item-text'>{props.children}</Col>
                </>
                break;
            case 'notice':
                return <>
                    <Col className='message-item-notice'>已设置{props.children}用药提醒</Col>
                </>
                break;
            case 'notice-del':
                return <>
                    <Col className='message-item-notice'>{props.children}</Col>
                </>
                break;
        }
    }

    render() {
        const { role } = this.props;
        return (
            <div className='message-item' role={role}>
                <Row
                    gutter={16}
                    justify={roleMap[role].align}>
                    {
                        this.getReactNode(role, this.props)
                    }

                </Row>

            </div>
        );
    }
}

Message.defaultProps = {
    role: 'self'
}

export default Message;